<template>
  <v-container
          fluid
          grid-list-lg
  >
    <v-layout row wrap>
      <v-flex xs12>
        <v-card color='#F5F5F5' class="mx-auto text-xs-center" min-width=100% >
          <v-card-title>
            <v-icon small left > directions_walk </v-icon>
            <span class="body-1 font-weight-light"></span>
          </v-card-title>
          <v-card-text class="display-3 font-weight-bold">
            {{stepNumbers}}
          </v-card-text>
          <v-divider></v-divider>
          <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
          <v-divider></v-divider>
          <div id="myChart2" :style="{width: '100%', height: '300px'}"></div>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  // 
  var echarts = require('echarts/lib/echarts');

  // 
  require('echarts/lib/chart/line');
  require('echarts/lib/chart/bar');

  // title
  require('echarts/lib/component/tooltip');
  require('echarts/lib/component/title');

  export default {
    data () {
      return {
        stepNumbers: 0,
        nowDate: '',
      }
    },
    created () {
      //localStorage
      this.nowDate = this.getNowDate();
      if (this.$store.state.sporttime.length > 0) {
        // 
        if (!this.$store.state.sportday.has(this.nowDate)) {
          this.$store.dispatch('removeSportTimeData');
          this.$store.dispatch('addSportTimeData', this.getNowTime());
          this.$store.dispatch('addSportDayData', this.nowDate);
        } else {
          this.stepNumbers = this.$store.state.sportday.get(this.nowDate);
        }
      } else {
        this.$store.dispatch('addSportTimeData', this.getNowTime());
        this.$store.dispatch('addSportDayData', this.nowDate);
      }

      this.stepCounter();
    },

    mounted(){
      this.drawLine();
    },

    methods: {
      drawLine(){
        // domecharts
        let myChart = echarts.init(document.getElementById('myChart'));
        // 
        let option1 = {
          title: {
            text: '',
            // subtext: '' + this.startTime,
            subtext: '' + this.$store.state.sporttime[0][0],
            x: '50%',
            y: '3%',
            textAlign: 'center'
          },
          xAxis: {
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            type: 'category',
            boundaryGap: false,
            // data: this.xKeys

          },
          yAxis: {
            // boundaryGap: [0, '50%'],
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            type: 'value'
          },
          series: [
            {
              // name:'',
              type:'line',
              smooth:true,
              // symbol: 'none',
              // stack: 'a',
              areaStyle: {
                normal: {}
              },
              // data: this.yValues
              data: this.$store.state.sporttime
            }
          ]
        };

        if (option1 && typeof option1 === "object") {
          myChart.setOption(option1, true);
        }

        setInterval(function () {
          let nowTime = this.getNowTime();
          if (nowTime !== this.$store.state.sporttime[this.$store.state.sporttime.length-1][0]) {
            this.$store.dispatch('addSportTimeData', nowTime)
          }
          myChart.setOption({
            // xAxis: {
            //   data: this.xKeys
            // },
            series: [{
              name:'',
              // data: this.yValues
              data: this.$store.state.sporttime
            }]
          });
        }.bind(this), 1000);

        let myChart2 = echarts.init(document.getElementById('myChart2'));
        let yMax = 100;
        let dataShadow = [];
        for (let value of this.$store.state.sportday.values()) {
          if (yMax < value) {
            yMax = ( Math.floor(value/100) + 1) * 100;
          }
        }

        for (let i = 0; i < this.$store.state.sportday.size; i++) {
          dataShadow.push(yMax);
        }

        let option2 = {
          title: {
            text: '',
            subtext: '' + this.$store.state.sportday.size + '',
            x: '50%',
            y: '3%',
            textAlign: 'center'
          },
          xAxis: {
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            data: Array.from(this.$store.state.sportday.keys()),
          },
          yAxis: {
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            // axisLabel: {
            //   textStyle: {
            //     color: '#999'
            //   }
            // }
          },
          dataZoom: [
            {
              type: 'inside'
            }
          ],
          series: [
            { // For shadow
              type: 'bar',
              itemStyle: {
                normal: {color: 'rgba(0,0,0,0.05)'}
              },
              barGap:'-100%',
              barCategoryGap:'40%',
              data: dataShadow,
              animation: false
            },
            {
              type: 'bar',
              itemStyle: {
                normal: {
                  color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            {offset: 0, color: '#FFCDD2'},
                            {offset: 0.5, color: '#E57373'},
                            {offset: 1, color: '#D32F2F'}
                          ]
                  )
                },
                emphasis: {
                  color: new echarts.graphic.LinearGradient(
                          0, 0, 0, 1,
                          [
                            {offset: 0, color: '#D32F2F'},
                            {offset: 0.7, color: '#E57373'},
                            {offset: 1, color: '#FFCDD2'}
                          ]
                  )
                }
              },
              data: Array.from(this.$store.state.sportday.values())
            }
          ]
        };

        if (option2 && typeof option2 === "object") {
          myChart2.setOption(option2, true);
        }
      },
      stepCounter: function () {
        if (window.DeviceMotionEvent) {
          window.addEventListener("devicemotion", motionHandler.bind(this), false);
        }

        //
        function motionHandler(event) {
          var accGravity = event.accelerationIncludingGravity;
          let nowTime = this.getNowTime();
          if (onSensorChanged(accGravity.x, accGravity.y, accGravity.z)) {
            this.$store.dispatch('updateSportTimeData', nowTime);
            this.$store.dispatch('updateSportDayData', this.nowDate);
            // TODO: Vuemapdata
            this.stepNumbers = this.$store.state.sportday.get(this.nowDate);
          }
        }

        //
        var oriValues = [];
        //
        var gravityNew = 0;
        function onSensorChanged(x, y, z) {
          oriValues[0] = x;
          oriValues[1] = y;
          oriValues[2] = z;
          gravityNew = Math.sqrt(oriValues[0] * oriValues[0] + oriValues[1] * oriValues[1] + oriValues[2] * oriValues[2]);
          return detectorNewStep(gravityNew);
        }

        /*
        * 
        * 1.sersor
        * 2.1
        * 3.initialValue
        * */
        //
        var gravityOld = 0;
        //
        var timeOfLastPeak = 0;
        //
        var timeOfThisPeak = 0;
        //
        var TimeInterval = 250;
        //
        var timeOfNow = 0;
        //
        var peakOfWave = 0;
        //
        var ThreadValue = 2.0;
        //
        var InitialValue = 1.3;
        function detectorNewStep(values) {
          let tmpcount = 0;
          if (gravityOld === 0) {
            gravityOld = values;
          } else {
            if (detectorPeak(values, gravityOld)) {
              timeOfLastPeak = timeOfThisPeak;
              timeOfNow = Date.now();
              // System.currentTimeMillis
              if (timeOfNow - timeOfLastPeak >= TimeInterval
                      && (peakOfWave - valleyOfWave >= ThreadValue)) {
                timeOfThisPeak = timeOfNow;
                /*
                 * 
                 * 
                 * 1.10
                 * 2.93
                 * 3.9
                 * */
                tmpcount++;
              }
              if (timeOfNow - timeOfLastPeak >= TimeInterval
                      && (peakOfWave - valleyOfWave >= InitialValue)) {
                timeOfThisPeak = timeOfNow;
                ThreadValue = peakValleyThread(peakOfWave - valleyOfWave);
              }
            }
          }
          gravityOld = values;

          return tmpcount > 0;
        }

        /*
        * 
        * 
        * 1.isDirectionUpfalse
        * 2.lastStatustrue
        * 3.2
        * 4.20
        * 
        * 1.
        * 2.
        * */
        //
        var lastStatus = false;
        //
        var isDirectionUp = false;
        //
        var continueUpCount = 0;
        //
        var continueUpFormerCount = 0;
        //
        var valleyOfWave = 0;
        function detectorPeak(newValue, oldValue) {
          lastStatus = isDirectionUp;
          if (newValue >= oldValue) {
            isDirectionUp = true;
            continueUpCount++;
          } else {
            continueUpFormerCount = continueUpCount;
            continueUpCount = 0;
            isDirectionUp = false;
          }

          if (!isDirectionUp && lastStatus
                  && (continueUpFormerCount >= 2 || oldValue >= 20)) {
            peakOfWave = oldValue;
            return true;
          } else if (!lastStatus && isDirectionUp) {
            valleyOfWave = oldValue;
            return false;
          } else {
            return false;
          }
        }

        /*
         * 
         * 1.
         * 2.4tempValue[]
         * 3.averageValue
         * */
        //
        var ValueNum = 4;
        var tempValue = new Array(ValueNum);
        var tempCount = 0;
        function peakValleyThread(value) {
          var tempThread = ThreadValue;
          if (tempCount < ValueNum) {
            tempValue[tempCount] = value;
            tempCount++;
          } else {
            tempThread = averageValue(tempValue, ValueNum);
            for (var i = 1; i < ValueNum; i++) {
              tempValue[i - 1] = tempValue[i];
            }
            tempValue[ValueNum - 1] = value;
          }
          return tempThread;
        }

        /*
         * 
         * 1.
         * 2.
         * */
        function averageValue(value, n) {
          var ave = 0;
          for (var i = 0; i < n; i++) {
            ave += value[i];
          }
          ave = ave / ValueNum;
          if (ave >= 8)
            ave = 4.3;
          else if (ave >= 7 && ave < 8)
            ave = 3.3;
          else if (ave >= 4 && ave < 7)
            ave = 2.3;
          else if (ave >= 3 && ave < 4)
            ave = 2.0;
          else {
            ave = 1.3;
          }
          return ave;
        }
      },
      //
      getNowDate: function () {
        let date = new Date();
        let month = date.getMonth()+ 1;
        let mm = month < 10 ? '0' + month : month;
        let dd = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        return mm + '/' + dd;
      },
      //
      getNowTime: function () {
        let date = new Date();
        let Hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        let Minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        return Hours + ':' + Minutes;
      }
    }
  }
</script>